@font-face {
  font-family: "Primary";
  src: url("../../../assets/fonts/roboto-regular-webfont.woff2") format("woff2"), url("../../../assets/fonts/roboto-regular-webfont.woff") format("woff"), url("../../../assets/fonts/roboto-regular-webfont.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Primary_italic";
  src: url("../../../assets/fonts/roboto-italic-webfont.woff2") format("woff2"), url("../../../assets/fonts/roboto-italic-webfont.woff") format("woff"), url("../../../assets/fonts/roboto-italic-webfont.ttf") format("truetype");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Primary_medium";
  src: url("../../../assets/fonts/roboto-medium-webfont.woff2") format("woff2"), url("../../../assets/fonts/roboto-medium-webfont.woff") format("woff"), url("../../../assets/fonts/roboto-medium-webfont.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Primary_bold";
  src: url("../../../assets/fonts/roboto-bold-webfont.woff2") format("woff2"), url("../../../assets/fonts/roboto-bold-webfont.woff") format("woff"), url("../../../assets/fonts/roboto-bold-webfont.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-font: "Primary", Arial, sans-serif;
  --primary-font-italic: "Primary_italic", Arial, sans-serif;
  --primary-font-medium: "Primary_medium", Arial, sans-serif;
  --primary-font-bold: "Primary_bold", Arial, sans-serif;
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: var(--primary-font);
  background-color: hsl(0, 0%, 90%);
}

.navbar {
  display: flex;
  justify-content: space-between;
}
.navbar__logo {
  width: 24px;
  display: flex;
  align-items: center;
  gap: 0.25em;
  font-size: 3.2rem;
}
.navbar__burger-icon {
  padding: 0.5em;
  cursor: pointer;
}
.navbar__links {
  display: none;
}